module.exports = "<!-- LIST -->\r\n<div class=\"row\">\r\n    <div class=\"col-md-6\">\r\n        <div class=\"input-group mb-2 mr-sm-2 mb-sm-0\">\r\n            <input type=\"text\" class=\"form-control\" id=\"inlineFormInputGroup\" placeholder=\"Buscar\" [(ngModel)]=\"search\" (keydown)=\"enter($event)\">\r\n            <div style=\"width: 34px\" class=\"input-group-addon\">\r\n                <a style=\"cursor:pointer\" (click)=\"getAll()\">\r\n                    <i class=\"fa fa-search\"></i>\r\n                </a>\r\n            </div>\r\n        </div>\r\n    </div>\r\n    <div class=\"col-md-6\" *ngIf=\"!noaction\">\r\n        <button md-raised-button (click)=\"NEW()\" color=\"primary\" class=\"btn-w-md no-margin-left btn-right\">Nuevo</button>\r\n    </div>\r\n\r\n    <div class=\"col-sm-12\">\r\n\r\n        <div class=\"box box-default table-box table-responsive mdl-shadow--2dp\">\r\n\r\n            <table class=\"mdl-data-table table-bordered table-striped cf no-margin\">\r\n                <thead>\r\n                    <tr>\r\n                        <th (click)=\"sort('pd.name')\">Nombre\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='pd.name'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n\r\n                        <th (click)=\"sort('ra.value')\">Va Administracin\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='ra.value'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n\r\n                        <th (click)=\"sort('ra.value')\">Forma Farmaceutica\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='ra.value'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n\r\n                        <th (click)=\"sort('is_pos')\">POS\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='is_pos'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n\r\n                        <th (click)=\"sort('pd.state')\">Estado\r\n                            <span class=\"glyphicon sort-icon\" *ngIf=\"key =='pd.state'\" [ngClass]=\"{'glyphicon-chevron-up':reverse,'glyphicon-chevron-down':!reverse}\"></span>\r\n                        </th>\r\n                        <th *ngIf=\"!noaction\"></th>\r\n\r\n                    </tr>\r\n                </thead>\r\n                <tbody>\r\n                    <tr *ngFor=\"let item of list.data\" (click)=\"view(item)\">\r\n                        <td>{{ item.name }}</td>\r\n                        <td>{{ item.routes_administration }}</td>\r\n                        <td>{{ item.dosage_form }}</td>\r\n                        <td>{{ item.is_pos ? 'Si' : 'No' }}</td>\r\n                        <td>{{ item.state ? 'Activo' : 'Inactivo' }}</td>\r\n                        <!-- <td>{{ item.state ? 'Activa' : 'Inactiva' }}</td> -->\r\n                        <td class=\"text-center\" *ngIf=\"!noaction\">\r\n                            <a style=\"color:#0a6ebd;cursor: pointer;\" (click)=\"view(item)\">\r\n                                <i class=\"fa fa-pencil fa-2x\"></i>\r\n                            </a>\r\n                        </td>\r\n                    </tr>\r\n                </tbody>\r\n            </table>\r\n\r\n        </div>\r\n        <ngb-pagination [pageSize]=\"pageSize\" [collectionSize]=\"paginationSize\" [(page)]=\"advancedPagination\" [maxSize]=\"6\" [rotate]=\"true\"\r\n            [ellipses]=\"false\" [boundaryLinks]=\"true\" (pageChange)=\"getAll()\"></ngb-pagination>\r\n\r\n    </div>\r\n</div>"

